.bigKana span {
    line-height: 1;
}

.bigKana {
    -ms-flex: 1 1;
        flex: 1 1;
}

.bigKana.overlay {
    position: absolute;
    -webkit-text-stroke: 5px var(--accent);
    -webkit-text-fill-color: transparent;
    pointer-events: none;
    visibility: hidden;
}

.bigKana.overlay.visible {
    visibility: visible;
}



